
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
body {
  background-color: #fff;
}
.recordBox {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  margin: 85px 30px 30px 30px;
  padding: 25px;
  border: 1px solid #ee735c;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 8px;
}
.recordIcon {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.recordIcon .Dogrecording {
  font-size: 30px;
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  padding-bottom: 20px;
}
.recordIcon .Dog {
  font-size: 80px;
}
.recordTitle {
  font-size: 18px;
}
.recordDesc {
  font-size: 14px;
  color: #999;
}
.videoBox {
  position: relative;
  width: 100%;
  height: 280px;
}
.videoBox .loadvideo {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 280px;
}
.videoBox .loadvideo .doglook {
  font-size: 80px;
  width: 80px;
  height: 100px;
  padding: 0;
  margin: 0;
}
.videoBox .loadvideo .loading {
  font-size: 30px;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
}
.videoBox .progressBox {
  position: absolute;
  background: rgba(244, 244, 244, 0.65);
  bottom: 0;
  left: 0;
  width: 100%;
}
.videoBox .progressBox .tips {
  width: 100%;
  height: 25px;
  font-size: 14px;
  text-align: center;
  line-height: 25px;
}



